.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 18rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

@media (max-width: 530px) {
  .header h1 {
    font-size: 1.8rem;
    line-height: 32px;
  }
}

@media (max-width: 470px) {
  .header h1 {
    font-size: 1.8rem;
    width: 100px;
  }
}

@media (max-width: 400px) {
  .header {
    padding: 0 5%;
  }
}